@import url(https://fonts.googleapis.com/css?family=Poppins);
:root {
  --color-bg: #16161d;
  --color-contrast-lower: #27272e;
  --color-contrast-low: #393940;
  --color-contrast-medium: #4d4d53;
  --color-contrast-high: #b8b8ba;
  --color-contrast-higher: #e7e7e8;

  --monospace: "JetBrains Mono", Menlo, Monaco, Consolas, "Liberation Mono",
    "Courier New", monospace, monospace;
  --color-outline: rgba(26, 255, 214, 0.6);

  --color-anchor: #1affd6;
  --color-shadow: #00000040;

  --secondary: #76757a !important;
  --danger: #ee0000 !important;
  --warning: #cb891d !important;
  --success: #1fc791 !important;
  --info: #00ffcd !important;
  --primary: #0070f3 !important;

  --secondary-hover: #616166;
  --danger-hover: #b60000;
  --warning-hover: #a16c17;
  --success-hover: #00bf80;
  --info-hover: #00b690;
  --primary-hover: #0050b6;
}

::-webkit-scrollbar-thumb {
  border-radius: 8px;
  background: #393940;
  background: var(--color-contrast-low);
}
::-webkit-scrollbar {
  width: 8px;
  background: #27272e;
  background: var(--color-contrast-lower);
}

::selection {
  background-color: rgba(53, 166, 247, 0.25);
}

body {
  position: relative;
  min-height: 100%;
  font-family: Poppins, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
    "Segoe UI Symbol" !important;
  text-rendering: optimizelegibility;
  -webkit-font-smoothing: antialiased;
}

html,
body {
  color: #e7e7e8 !important;
  color: var(--color-contrast-higher) !important;
  background-color: #16161d !important;
  background-color: var(--color-bg) !important;
}

code {
  font-family: "JetBrains Mono", Menlo, Monaco, Consolas, "Liberation Mono",
    "Courier New", monospace, monospace;
  font-family: var(--monospace);
}

hr {
  border-top: 1px solid #4d4d53 !important;
  border-top: 1px solid var(--color-contrast-medium) !important;
}

a,
a:hover {
  color: #1affd6 !important;
  color: var(--color-anchor) !important;
}

header {
  padding-top: 48px;
  padding-bottom: 48px;
  background: linear-gradient(0deg, #16161d40, #13197340);
}

main {
  padding-top: 24px;
  padding-bottom: 24px;
}

section {
  padding-top: 16px;
}
section:not(:last-child) {
  padding-bottom: 16px;
}
section.child {
  padding-top: 8px;
}
section.child:not(:last-child) {
  padding-bottom: 8px;
}

time {
  font-family: "JetBrains Mono", Menlo, Monaco, Consolas, "Liberation Mono",
    "Courier New", monospace, monospace;
  font-family: var(--monospace);
}

.row {
  justify-content: center;
}

.note {
  cursor: pointer;
}
.note:not(:last-child) {
  margin-bottom: 8px;
}
.note .options-trigger {
  position: absolute;
  right: 0;
  bottom: 0;
  padding: 8px;
  transform: rotate(90deg);
  font-weight: 700;
  letter-spacing: 3px;
  border: 0;
  background: transparent;
  outline: none;
  color: #e7e7e8;
  color: var(--color-contrast-higher);
}
.note .options {
  display: none;
}
.note .options.show {
  display: block;
}
.note .options {
  z-index: 1001;
  position: absolute;
  right: 8px;
  bottom: 8px;
  border-radius: 6px;
  padding: 10px 0;
  background: #393940;
  background: var(--color-contrast-low);
  -webkit-animation: opacity 55ms linear;
          animation: opacity 55ms linear;
}
@-webkit-keyframes opacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes opacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.note .options ul {
  margin: 0;
  padding: 0;
}
.note .options ul li {
  padding: 3px 33px 3px 24px;
  min-width: 89px;
  list-style: none;
}
.note .options ul li:hover {
  background: #4d4d53;
  background: var(--color-contrast-medium);
}
.note .options ul li:hover:active,
.note .options ul li:hover:focus {
  color: #4d4d53;
  color: var(--color-contrast-medium);
  background: #b8b8ba;
  background: var(--color-contrast-high);
}

.navigation {
  position: fixed;
  bottom: 24px;
  right: 24px;
  padding: 9px;
  border-radius: 18px;
  background: #27272e;
  background: var(--color-contrast-lower); 
}
.navigation .navi:not(:last-child) {
  margin-right: 6px;
}

.input-title,
.input-body {
  width: 100%;
  border: 0;
  outline: none;
  color: #e7e7e8;
  color: var(--color-contrast-higher);
  background: transparent;
}
.input-body {
  height: 60vh;
}

h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6,
p {
  margin: 0;
}

h1,
.h1 {
  font-size: 32px;
  line-height: 36px;
  margin-bottom: 8px;
  font-weight: 600;
}
h2,
.h2 {
  font-size: 24px;
  line-height: 28px;
  margin-bottom: 8px;
  font-weight: 600;
}
h3,
.h3 {
  font-size: 22px;
  line-height: 26px;
  margin-bottom: 8px;
  font-weight: 600;
}
h4,
.h4 {
  font-size: 20px;
  line-height: 24px;
  margin-bottom: 8px;
  font-weight: 600;
}
h5,
.h5 {
  font-size: 18px;
  line-height: 22px;
  margin-bottom: 8px;
  font-weight: 600;
}
h6,
.h6 {
  font-size: 16px;
  line-height: 20px;
  margin-bottom: 8px;
  font-weight: 600;
}
@media (min-width: 992px) {
  h1,
  .h1 {
    font-size: 44px;
    line-height: 48px;
  }
  h2,
  .h2 {
    font-size: 32px;
    line-height: 36px;
  }
  h3,
  .h3 {
    font-size: 28px;
    line-height: 32px;
  }
  h4,
  .h4 {
    font-size: 24px;
    line-height: 28px;
  }
  h5,
  .h5 {
    font-size: 20px;
    line-height: 24px;
  }
  h6,
  .h6 {
    font-size: 16px;
    line-height: 20px;
  }
}

.btn {
  border-radius: 12px !important;
  box-shadow: unset !important;
  transition: background 200ms, border-color 200ms;
}
.btn:active {
  transition: 100ms transform ease;
  transform: scale3d(0.96, 0.96, 0.96);
}
.btn:disabled,
.btn:disabled:hover {
  cursor: not-allowed;
  color: var(--color-contrast-high) !important;
  border-color: var(--color-contrast-low) !important;
  background: var(--color-contrast-low) !important;
}

.btn-link,
.btn-link:hover {
  color: var(--color-anchor);
}

.btn-outline,
[class*="btn-outline-"] {
  color: var(--color-contrast-higher);
  border-width: 4px !important;
  border-color: var(--color-contrast-higher);
  background: transparent;
  box-shadow: unset !important;
}
.btn-outline:hover,
[class*="btn-outline-"]:hover {
  color: var(--color-bg) !important;
}
.btn-outline:active,
[class*="btn-outline-"]:active {
  color: var(--color-bg);
  border-color: var(--info) !important;
}

.btn-outline-secondary {
  border-color: var(--secondary);
  color: var(--color-contrast-higher);
}
.btn-outline-danger {
  color: var(--danger);
}
.btn-outline-warning {
  color: var(--warning);
}
.btn-outline-success {
  color: var(--success);
}
.btn-outline-info {
  color: var(--info);
}
.btn-outline-primary {
  color: var(--primary);
}

.btn-outline-secondary:hover {
  border-color: var(--secondary);
  background: var(--secondary) !important;
}
.btn-outline-danger:hover {
  border-color: var(--danger);
  background: var(--danger) !important;
}
.btn-outline-warning:hover {
  border-color: var(--warning);
  background: var(--warning) !important;
}
.btn-outline-success:hover {
  border-color: var(--success);
  background: var(--success) !important;
}
.btn-outline-info:hover {
  border-color: var(--info);
  background: var(--info) !important;
}

.btn-secondary {
  background: var(--secondary);
}
.btn-danger {
  background: var(--danger);
}
.btn-warning {
  background: var(--warning);
}
.btn-success {
  background: var(--success);
}
.btn-info {
  background: var(--info);
}
.btn-primary {
  background: var(--primary);
}

.btn-secondary:hover {
  background: var(--secondary-hover);
}
.btn-danger:hover {
  background: var(--danger-hover);
}
.btn-warning:hover {
  background: var(--warning-hover);
}
.btn-success:hover {
  background: var(--success-hover);
}
.btn-info:hover {
  background: var(--info-hover);
}
.btn-primary:hover {
  background: var(--primary-hover);
}

.text-secondary {
  color: var(--secondary) !important;
}
.text-danger {
  color: var(--danger) !important;
}
.text-warning {
  color: var(--warning) !important;
}
.text-success {
  color: var(--success) !important;
}
.text-info {
  color: var(--info) !important;
}
.text-primary {
  color: var(--primary) !important;
}

.bg-secondary {
  background: var(--secondary) !important;
}
.bg-danger {
  background: var(--danger) !important;
}
.bg-warning {
  background: var(--warning) !important;
}
.bg-success {
  background: var(--success) !important;
}
.bg-info {
  background: var(--info) !important;
}
.bg-primary {
  background: var(--primary) !important;
}

.card {
  border: 0;
  border-radius: 6px;
  border-color: var(--color-contrast-lower);
  box-shadow: var(--color-shadow) 0px 4px 8px 0px;
  background: var(--color-contrast-lower);
}
.card-footer {
  background: var(--color-contrast-lower);
}
.card-footer:last-child {
  border-radius: 0 0 6px 6px;
}

.form-control,
.form-control:focus {
  color: var(--color-contrast-higher);
  background: var(--color-bg) !important;
}
.form-control:focus {
  box-shadow: unset;
}

.table {
  color: var(--color-contrast-higher);
}
.table-hover tbody tr:hover {
  color: var(--color-contrast-high);
  background: var(--color-contrast-lower);
}

